<template>
  <div class="room">
    <div class="bunker">
      <h1>Бункер</h1>
      <p>Размер: {{ bunker.size }}</p>
      <p style="display: flex">Спец комнаты: <ul style="list-style: none; margin-left: 5px;"><li v-for="(room, i) in bunker.rooms" :key="i">{{ room }}</li></ul></p>
      <p>Оборудование: {{ bunker.tech }}</p>
    </div>
    <div class="flexeble">
      <div class="wrapper">
      <div class="player">
        <h2>{{ player.name }}</h2>
        <div class="profession">
          <p>{{ player.profession }}</p>
        </div>
        <div class="showWrapper">
          <p>Пол:</p>
          <button @click="player.checked.gender = true; check()">
            {{ player.gender }}
            <i class="fas fa-check" v-if="player.checked.gender"></i>
          </button>
        </div>
        <div class="showWrapper">
          <p>Возраст:</p>
          <button @click="player.checked.age = true; check()">
            {{ player.age }}
            <i class="fas fa-check" v-if="player.checked.age"></i>
          </button>
        </div>
        <div class="showWrapper">
          <p>Хобби:</p>
          <button @click="player.checked.hobby = true; check()">
            {{ player.hobby }}
            <i class="fas fa-check" v-if="player.checked.hobby"></i>
          </button>
        </div>
        <div class="showWrapper">
          <p>Здоровье: </p>
          <button @click="player.checked.sickness = true; check()">
            {{ player.sickness }}
            <i class="fas fa-check" v-if="player.checked.sickness"></i>
          </button>
        </div>
        <div class="showWrapper">
          <p>Фобия: </p>
          <button @click="player.checked.psycho = true; check()">
            {{ player.psycho }}
            <i class="fas fa-check" v-if="player.checked.psycho"></i>
          </button>
        </div>
      </div>
    </div>
    <div class="player" v-for="(user, i) in users" :key="i">
      <h2>{{ user.name }}</h2>
      <p>{{ user.profession }}</p>
      <p v-if="user.checked.gender">Пол: {{ user.gender }}</p>
      <p v-if="user.checked.age">Возраст: {{ user.age }}</p>
      <p v-if="user.checked.hobby">Хобби: {{ user.hobby }}</p>
      <p v-if="user.checked.psycho">Фобия: {{ user.psycho }}</p>
      <p v-if="user.checked.sickness">Здоровье: {{ user.sickness }}</p>
    </div>
    </div>
    
  </div>
</template>

<script>
import db from "../fb.js";

export default {
  name: "Room",
  data: () => ({
    users: {},
    player: {},
    bunker: {}
  }),
  firestore: {
    users: db.collection("users"),
    player: db.collection("users").doc(
      (function() {
        if (JSON.parse(localStorage.getItem("user"))) {
          console.log("YES");
          return JSON.parse(localStorage.getItem("user"));
        } else {
          console.log("NO");
          return "Onime";
        }
      })()
    ),
    bunker: db.collection('world').doc('bunker')
  },
  methods: {
    check() {
      db.collection("users")
        .doc(JSON.parse(localStorage.getItem("user")))
        .update({
          checked: {
            hobby: this.player.checked.hobby,
            profession: this.player.checked.profession,
            psycho: this.player.checked.psycho,
            inventory: this.player.checked.inventory,
            sickness: this.player.checked.sickness,
            gender: this.player.checked.gender,
            age: this.player.checked.age
          }
        });
    }
  }
};
</script>

